
'use client';

import * as React from 'react';
import Header from "@/components/layout/header";
import Footer from "@/components/layout/footer";
import SocialBar from "@/components/layout/social-bar";
import Image from "next/image";
import {
  Dialog,
  DialogContent,
  DialogTrigger,
} from '@/components/ui/dialog';
import { cn } from '@/lib/utils';

const imageUrl = "https://i.postimg.cc/C5JK41T1/BANNER-PPF.jpg";

export default function PPFPage() {
  const [isDialogOpen, setDialogOpen] = React.useState(false);
  const [scale, setScale] = React.useState(1);
  const [position, setPosition] = React.useState({ x: 0, y: 0 });
  const [isDragging, setIsDragging] = React.useState(false);
  const [startPos, setStartPos] = React.useState({ x: 0, y: 0 });
  const [initialDistance, setInitialDistance] = React.useState<number | null>(null);
  const imageRef = React.useRef<HTMLImageElement>(null);

  const getDistance = (touches: React.TouchList) => {
    const [touch1, touch2] = touches;
    return Math.sqrt(
      Math.pow(touch2.clientX - touch1.clientX, 2) +
      Math.pow(touch2.clientY - touch1.clientY, 2)
    );
  };

  const handleWheel = (e: React.WheelEvent) => {
    e.preventDefault();
    const newScale = scale - e.deltaY * 0.001;
    setScale(Math.min(Math.max(1, newScale), 3));
  };
  
  const handleMouseDown = (e: React.MouseEvent) => {
    if (scale > 1) {
      e.preventDefault();
      setIsDragging(true);
      setStartPos({
        x: e.clientX - position.x,
        y: e.clientY - position.y,
      });
    }
  };

  const handleMouseMove = (e: React.MouseEvent) => {
    if (isDragging && scale > 1) {
      e.preventDefault();
      setPosition({
        x: e.clientX - startPos.x,
        y: e.clientY - startPos.y,
      });
    }
  };

  const handleMouseUp = (e: React.MouseEvent) => {
    e.preventDefault();
    setIsDragging(false);
  };

  const handleTouchStart = (e: React.TouchEvent) => {
    if (e.touches.length === 2) {
      setInitialDistance(getDistance(e.touches));
      setIsDragging(false);
    } else if (e.touches.length === 1 && scale > 1) {
      setIsDragging(true);
      const touch = e.touches[0];
      setStartPos({
        x: touch.clientX - position.x,
        y: touch.clientY - position.y,
      });
    }
  };

  const handleTouchMove = (e: React.TouchEvent) => {
    if (e.touches.length === 2 && initialDistance !== null) {
      const newDistance = getDistance(e.touches);
      const newScale = scale * (newDistance / initialDistance);
       setScale(Math.min(Math.max(1, newScale), 3));
      setInitialDistance(newDistance);
    } else if (e.touches.length === 1 && isDragging && scale > 1) {
      const touch = e.touches[0];
      setPosition({
        x: touch.clientX - startPos.x,
        y: touch.clientY - startPos.y,
      });
    }
  };

  const handleTouchEnd = () => {
    setIsDragging(false);
    setInitialDistance(null);
  };
  
  const resetZoom = () => {
    setScale(1);
    setPosition({ x: 0, y: 0 });
  }

  React.useEffect(() => {
    if (!isDialogOpen) {
      setTimeout(resetZoom, 200);
    }
  }, [isDialogOpen]);

  return (
    <div className="flex min-h-screen flex-col">
      <Header />
      <SocialBar />
      <main className="flex-1">
        <section className="py-20 sm:py-32 bg-background">
          <div className="container">
            <div className="grid md:grid-cols-2 gap-12 items-center">
              <div className="md:col-span-1">
                 <Dialog open={isDialogOpen} onOpenChange={setDialogOpen}>
                    <DialogTrigger asChild>
                        <div className="cursor-pointer overflow-hidden rounded-lg">
                            <Image
                                src={imageUrl}
                                alt="PPF Banner"
                                width={500}
                                height={700}
                                className="w-full h-auto rounded-lg transition-transform duration-300 hover:scale-105"
                                data-ai-hint="ppf application"
                            />
                        </div>
                    </DialogTrigger>
                    <DialogContent 
                      className="max-w-5xl p-0 border-0 overflow-hidden" 
                      onMouseUp={handleMouseUp}
                      onMouseMove={handleMouseMove}
                      onTouchStart={handleTouchStart}
                      onTouchMove={handleTouchMove}
                      onTouchEnd={handleTouchEnd}
                    >
                        <div 
                          className={cn(
                            "overflow-hidden touch-none",
                            scale > 1 && isDragging ? 'cursor-grabbing' : (scale > 1 ? 'cursor-grab' : 'cursor-default')
                          )}
                          onWheel={handleWheel}
                          onMouseDown={handleMouseDown}
                        >
                          <Image
                              ref={imageRef}
                              src={imageUrl}
                              alt="PPF Banner Ampliado"
                              width={1200}
                              height={800}
                              className="h-auto w-full object-contain transition-transform duration-200"
                              style={{
                                  transform: `scale(${scale}) translate(${position.x}px, ${position.y}px)`,
                                  transformOrigin: 'center center'
                              }}
                          />
                        </div>
                    </DialogContent>
                </Dialog>
              </div>
              <div className="md:col-span-1">
                 <h1 className="font-headline text-4xl font-black uppercase text-primary md:text-5xl">
                    Paint Protection Film (PPF)
                </h1>
                <p className="mt-6 font-body text-lg leading-relaxed text-foreground">
                    La película de protección de pintura (PPF) es una lámina transparente y prácticamente invisible que se aplica sobre la carrocería del vehículo para protegerla contra los daños más comunes del uso diario. Este recubrimiento de poliuretano de alta tecnología es autocurable, lo que significa que puede eliminar micro-rayones con el calor del sol o con una pistola de calor, manteniendo siempre un acabado impecable.
                </p>
              </div>
            </div>

            <div className="mt-20 text-center">
                <h2 className="font-headline text-3xl font-black uppercase text-primary md:text-4xl">
                    Beneficios del PPF
                </h2>
                <ul className="mt-6 font-body text-lg text-left leading-relaxed text-foreground mx-auto max-w-3xl list-disc pl-5">
                    <li>Protección contra rayones, piedras, insectos, excremento de aves, resina de árboles y otros contaminantes.</li>
                    <li>Alta resistencia a impactos y desgaste.</li>
                    <li>No altera el color ni el brillo original del vehículo.</li>
                    <li>Disponible en acabado transparente gloss o matte.</li>
                    <li>Larga duración: hasta 10 años con el mantenimiento adecuado.</li>
                    <li className="list-none mt-2">En Luxtor Detail Studio, utilizamos películas de las mejores calidades del mercado y aplicamos cada pieza con máxima precisión, asegurando cobertura perfecta y un acabado limpio y profesional.</li>
                </ul>
            </div>
          </div>
        </section>
      </main>
      <Footer />
    </div>
  );
}
